@charset "utf-8";

@import "@fortawesome/fontawesome-free/css/all.min.css";
@import "fixed-data-table-2/dist/fixed-data-table.min.css";

$black: #4a4a4a !default;

$gray: #bebebe !default;
$lightGray: #f5f5f5 !default;
$lighterGray: #f7f7f7 !default;
$darkGray: #b4b4b4 !default;

$blue: rgb(37, 100, 225) !default;
$lightBlue: rgb(233, 240, 255) !default;
$lighterBlue: #f4feff !default;
$darkBlue: rgb(33, 83, 185) !default;

$green: rgb(19, 217, 46) !default;
$lightGreen: rgb(202, 255, 225) !default;
$darkGreen: rgb(16, 191, 39) !default;

$red: rgb(241, 80, 80) !default;
$lightRed: rgb(255, 238, 238) !default;
$darkRed: rgb(172, 31, 31) !default;

$orange: rgb(208, 156, 0) !default;
$lightOrange: rgb(251, 246, 227) !default;
$darkOrange: rgb(230, 163, 28) !default;

$backgroundLight: $lightGray !default;
$activeItem: $blue !default;

// reset

/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}

body {
    line-height: 1;
}

ol,
ul {
    list-style: none;
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
    content: '';
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

html {
    overflow: hidden;
}

body {
    overflow: hidden;
}

// basic customizations

b {
    font-weight: bold;
}

h1,
h2,
h3,
h4,
h5 {
    font-weight: bold;
}

h1 {
    font-size: 2em;
}

h2 {
    font-size: 1.75em;
}

h3 {
    font-size: 1.5em;
}

h4 {
    font-size: 1.25em;
}

h5 {
    font-size: 1em;
}

dl dt {
