}

.diamond-field-addon {
    margin: 2px;
    padding: 2px;
    width: 1em;
    height: 1em;
    font-size: 10pt;
}

.diamond-field-addon-button {
    cursor: pointer;
    background-color: $lightGray;
    border: 1px solid $gray;
    margin: 0;
    padding: 2px;
    overflow: hidden;
}


.diamond-field:hover {
    outline: 1px solid $gray;
}

.diamond-field-select {
    background-color: inherit;
    cursor: pointer;
}

.diamond-field-select>select {
    background-color: inherit;
    cursor: pointer;
}

.diamond-field:focus-within {
    outline: 1px solid $gray;
    box-shadow: 0 0 0 0.1em $lightGray;
}


input,
select,
textarea {
    border: none;
    font-family: inherit;
}

input:focus,
select:focus,
textarea:focus {
    outline: none;
}

.diamond-field.diamond-ok {
    border-color: $green;
    outline-color: $green;
}

.diamond-ok .diamond-field-addon-icon {
