}

.diamond-field.diamond-error {
    border-color: $red;
    outline-color: $red;
}

.diamond-error .diamond-field-addon-icon {
    color: $red;
}

.diamond-field.diamond-warning {
    border-color: $orange;
    outline-color: $orange;
