// components specific styles

.diamond-main {
    overflow: hidden;
    height: 100vh;
    max-height: 100vh;
    width: 100vw;
    max-width: 100vw;
    display: flex;
    flex-grow: 1;
}

// panel components

.diamond-layout-fit {
    display: flex !important;
    box-sizing: border-box;
    max-height: 100%;
    align-items: stretch;
}

.diamond-layout-hbox {
    display: flex !important;
    flex-direction: row;
    max-height: 100%;
    align-items: stretch;
}

.diamond-layout-vbox {
    display: flex !important;
    flex-direction: column;
    max-height: 100%;
    align-items: stretch;
}

.diamond-layout-card {
    display: flex !important;
    box-sizing: border-box;
    max-height: 100%;
    align-items: stretch;
}

.diamond-layout-fit>* {
    flex-grow: 1;
}

.diamond-layout-card>* {
    flex-grow: 0;
    display: none;
}

.diamond-layout-card>.is-active {
    flex-grow: 1;
    display: flex;
}

.diamond-panel-header {
    padding: 5px;
}

.diamond-panel-header>* {
    padding-right: 5px;
}

.diamond-panel-footer {
    padding: 5px;
}

.diamond-panel-footer>* {
    padding-right: 5px;
}

.diamond-collapsible-panel {
    border-color: $lightGray;
    border-style: solid;
    border-width: 1px;
}

.diamond-collapsible-panel-button {
    background-color: $lightGray;
    border-color: $lightGray;
    border-style: solid;
    border-left-width: 1px;
    padding-left: 1px;
    padding-right: 1px;
}

.diamond-collapsible-panel-button:hover {
    background-color: $lighterGray;
}

.diamond-boxed {
    box-shadow: 0 0.5em 1em -0.125em rgb(10 10 10 / 10%), 0 0px 0 1px rgb(10 10 10 / 2%);
    border-radius: 6px;
}

.diamond-dialog-header {
    padding: 10px;
    justify-content: space-between;
    border-style: solid;
    border-bottom-width: 1px;
    border-bottom-color: $lightGray;
}

.diamond-modal-dialog {
    background-color: rgba(248, 248, 248, 0.85);
    z-index: 99999;
}

.diamond-dialog-footer {
    background-color: $lighterGray;
    padding: 10px;
}

// lazy table

.public_fixedDataTableRow_highlighted,
.public_fixedDataTableRow_highlighted .public_fixedDataTableCell_main {
    background-color: $lighterBlue;
    ;
}

.public_fixedDataTable_header,
.public_fixedDataTable_scrollbarSpacer,
.public_fixedDataTable_header .public_fixedDataTableCell_main {
