}

.diamond-lazytable-row-selected * {
    background-color: $blue !important;
    color: white;
}

.diamond-lazytable-row-warning * {
    background-color: rgb(255, 235, 235);
}

.diamond-lazytable-footer {
    background-color: $lighterGray;
}

// menu

.diamond-menu {
    margin: 5px;
}

.diamond-menu-item {
    padding: 7px;
    cursor: default;

}

.diamond-menu-item a {
    cursor: pointer;
    color: inherit;
    text-decoration: inherit;
}

.diamond-menu-item a:visited {
    cursor: pointer;
    color: inherit;
    text-decoration: inherit;
}

.diamond-menu-item:hover {
    background-color: $backgroundLight;
}

.diamond-menu-item.is-active {
    background-color: $activeItem;
    color: white;
}

.diamond-menu-group-caption {
    text-transform: uppercase;
    font-weight: bold;
    font-size: 0.9em;
    color: $gray;
    cursor: default;
    margin-top: 5px;
    margin-bottom: 5px;
}

// overlay

.diamond-lds-ring {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
}

.diamond-lds-ring div {
    box-sizing: border-box;
    display: block;
    position: absolute;
    width: 64px;
    height: 64px;
    margin: 8px;
    border: 8px solid rgb(158, 214, 221);
    border-radius: 50%;
    animation: diamond-lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    border-color: rgb(158, 214, 221) transparent transparent transparent;
}

.diamond-lds-ring div:nth-child(1) {
    animation-delay: -0.45s;
}

.diamond-lds-ring div:nth-child(2) {
    animation-delay: -0.3s;
}

.diamond-lds-ring div:nth-child(3) {
    animation-delay: -0.15s;
}

@keyframes diamond-lds-ring {
    0% {
        transform: rotate(0deg);
    }

    100% {
        transform: rotate(360deg);
    }
}

.diamond-overlay-loader-text {
    font-weight: bold;
    color: rgb(41, 41, 186);
}

// alert

.diamond-alert {
    display: flex;
    flex-direction: row;
    border-radius: 1rem;
    padding: 1rem;
    border-style: solid;
    border-width: 1px;
}

.diamond-alert>* {
    padding-left: 3px;
    padding-right: 3px;
