}

.diamond-alert.diamond-warning {
    background-color: $orange;
    color: $lightOrange;
}

.diamond-alert.diamond-warning.is-light {
    background-color: $lightOrange;
    color: $orange;
}

.diamond-alert.diamond-error {
    background-color: $red;
    color: $lightRed;
}

.diamond-alert.diamond-error.is-light {
    background-color: $lightRed;
