.with-tooltip {
    position: relative;
    border-bottom: 1px dashed $lightBlue;
}

.with-tooltip:before {
    content: attr(data-text);
    position: absolute;

    /* vertically center */
    top: 50%;
    transform: translateY(-50%);

    /* move to right */
    left: 100%;
    margin-left: 15px;
    /* and add a small left margin */

    /* basic styles */
    width: 200px;
    padding: 10px;
    border-radius: 10px;
    background: $blue;
    color: #fff;
    text-align: center;

    opacity: 0;
    transition: 0.3s opacity;
    visibility: hidden;
}

.with-tooltip:hover:before {
    display: block;
    opacity: 1;
    visibility: visible;
}

.with-tooltip:after {
    content: "";
    position: absolute;

    /* position tooltip correctly */
    left: 100%;
    margin-left: 0px;

    /* vertically center */
    top: 50%;
    transform: translateY(-50%);

    /* the arrow */
    border: 10px solid $blue;
    border-color: transparent $blue transparent transparent;

    opacity: 0;
    transition: 0.3s opacity;
    visibility: hidden;
}

.with-tooltip:hover:before,
.with-tooltip:hover:after {
    display: block;
    opacity: 1;
    visibility: visible;
}

.with-tooltip.tooltip-left:before {
    /* reset defaults */
    left: initial;
    margin: initial;

    /* set new values */
    right: 100%;
    margin-right: 10px;
}

.with-tooltip.tooltip-left:after {
    left: initial;
    margin-left: -25px;

    /* vertically center */
    top: 50%;
    transform: translateY(-50%);

    /* the arrow */
    border-color: transparent transparent transparent $blue;
}

.with-tooltip.tooltip-top:before {
    /* reset defaults */
    top: initial;
    margin: initial;

    /* set new values */
    left: -50%;
    top: -50%;
    margin-top: -15px;
}

.with-tooltip.tooltip-top:after {
    left: 50%;

    /* vertically center */
    transform: translateY(50%);
    top: -100%;
    margin-top: 10px;

    /* the arrow */
    border-color: $blue transparent transparent transparent;
}

.with-tooltip.tooltip-bottom:before {
    transform: translateY(50%);
    margin-bottom: -15px;
    /* set new values */
    left: -50%;
    top: 100%;
}

.with-tooltip.tooltip-bottom:after {
    left: 50%;

    /* vertically center */
    transform: translateY(50%);
    top: 100%;
    margin-top: -10px;

    /* the arrow */
    border-color: transparent transparent $blue transparent;
}